// SECTION subscribers :: START

.subscribers {
	padding-top: 120px;
	padding-bottom: 120px;

	.main__wrapper {
		z-index: 2;
		position: relative;
	}

	&__section {
		margin-bottom: 20px;
		border-radius: 6px;
		box-shadow: 0 1px 20px 0 rgba(17, 30, 79, 0.1);
		background-color: #ffffff;

		&-title {
			font-size: 20px;
			font-weight: 600;
			letter-spacing: 0.47px;

			span {
				font-weight: normal;
			}
		}

		&-btn {
			a {
				width: 100%;
				max-width: 177px;
				height: 40px;
			}
		}
	}

	// tabs
	&__tabs {
		height: 56px;

		> div,
		> span {
			height: 100%;
			text-align: center;
		}

		> div {
			width: 100%;
			max-width: calc((100% - 60px) / 3);
		}

		> span {
			position: relative;
			width: 30px;
			height: calc(100% - 2rem);

			&:after {
				content: '';
				position: absolute;
				top: 0;
				left: 50%;
				transform: translateX(-50%);
				width: 1px;
				height: 100%;
				background-color: #979797;
			}
		}

		&-btn {
			overflow: hidden;
			height: 100%;
			font-size: 15px;
			font-weight: 500;
			letter-spacing: 0.47px;
			text-align: center;
			@include adaptive(575) {
				justify-content: center;
			}

			&.is-active {
				color: #6772e5;

				&:after {
					opacity: 1;
					@include transition(opacity);
				}
			}

			&:after {
				opacity: 0;
				content: '';
				position: absolute;
				bottom: 0;
				width: 100%;
				height: 2px;
				border: solid 1px #6772e5;
				background-color: #d8d8d8;
				@include transition(opacity);
			}
		}
	}
	// tabs :: end

	// self
	&__self {
		&-head {
			a {
				font-size: 18px;
				letter-spacing: 0.43px;
			}
		}

		&-footer {
			min-height: 71px;
			border-bottom-left-radius: 6px;
			border-bottom-right-radius: 6px;
			background-color: #6772e5;

			> div {
				width: 100%;
				max-width: calc(100% / 4);
				text-align: center;

				&:first-of-type {
					text-align: left;
				}

				&:last-of-type {
					&:after {
						display: none;
					}
				}

				&:after {
					content: '';
					position: absolute;
					top: 0;
					right: 0;
					width: 1px;
					height: calc(100%);
					background-color: #ffffff;
				}
			}

			p {
				letter-spacing: 0.27px;
				color: #ffffff;
			}
		}
	}
	// self :: end

	// reversion
	&__reversion {
		&-info {
			font-size: 16px;
			letter-spacing: 0.38px;
		}

		table {
			.nowrap {
				white-space: nowrap;
			}

			thead,
			tbody {
				th,
				td {
					border-right: 1px solid #dee2e6;
					&:last-of-type {
						border-right: none;
					}

					@include adaptive(767) {
						min-width: 150px;
					}

					&:first-of-type {
						padding-left: 0;
					}
				}
			}

			thead {
				th {
					border-bottom-width: 1px;
					font-size: 14px;
					font-weight: bold;
				}
			}

			tbody {
				tr {
					&:last-of-type {
						td {
							border-right: none;
						}
					}
				}

				td {
					p {
						span {
							font-weight: 500;
						}
					}

					table {
						thead,
						tbody {
							th,
							td {
								padding: 0.25rem 0.5rem;
								border: none;
							}
						}

						thead {
							th {
								border-top: none;
								border-bottom: none;
							}
						}

						tbody {
							tr {
								&:last-of-type {
									td {
										border-bottom: none;
									}
								}
							}

							td {
								border-top: none;
								border-bottom: none;
							}
						}
					}
				}
			}
		}
	}
	// reversion :: end

	// midbass
	&__midbass {
		&-info {
			font-size: 16px;
			letter-spacing: 0.38px;
		}

		table {
			.nowrap {
				white-space: nowrap;
			}

			thead,
			tbody {
				th,
				td {
					border-right: 1px solid #dee2e6;
					&:last-of-type {
						border-right: none;
					}

					@include adaptive(767) {
						min-width: 150px;
					}

					&:first-of-type {
						padding-left: 0;
					}
				}
			}

			thead {
				th {
					border-bottom-width: 1px;
					font-size: 14px;
					font-weight: bold;
				}
			}

			tbody {
				tr {
					&:last-of-type {
						td {
							border-right: none;
						}
					}
				}

				td {
					p {
						span {
							font-weight: 500;
						}
					}

					table {
						thead,
						tbody {
							th,
							td {
								padding: 0.25rem 0.5rem;
								border: none;
							}
						}

						thead {
							th {
								border-top: none;
								border-bottom: none;
							}
						}

						tbody {
							tr {
								&:last-of-type {
									td {
										border-bottom: none;
									}
								}
							}

							td {
								border-top: none;
								border-bottom: none;
							}
						}
					}
				}
			}
		}
	}
	// midbass :: end

	// total
	&__total {
		> div {
			width: 100%;
			max-width: calc(100% / 3);
		}

		p {
			a {
				color: #6772e5;
			}
		}
	}
	// total :: end

	// broadcast
	&__broadcast {
		&-wrapper {
			&-left {
				width: 280px;
				padding-right: 30px;
			}

			&-right {
				flex: 1;
				min-width: 1px;
				max-width: 100%;
			}

			.dropdown {
				width: 100%;

				&-toggle {
					display: flex;
					align-items: center;
					justify-content: space-between;
					width: 100%;
					height: 40px;
					box-shadow: none;
					border-radius: 6px;
					border: solid 1px rgba(185, 185, 185, 0.3);
					background-color: #ffffff;

					&:focus {
						box-shadow: none;
						border-color: rgba(185, 185, 185, 0.3);
					}
				}

				&-menu {
					width: 100%;

					input {
						height: auto;
					}

					label {
						position: relative;
						top: 2px;
						letter-spacing: 0.27px;
						font-weight: normal;
					}
				}
			}

			textarea {
				resize: none;
				min-height: 136px;
				letter-spacing: 0.09px;
				@include inputPlaceholder {
					color: rgba(#333333, 0.54);
				}
			}
		}

		&-attach {
			cursor: pointer;
			width: 100%;
			min-height: 80px;
			border-radius: 6px;
			border: 1px dashed #B0C4E0;

			input {
				cursor: pointer;
				z-index: 10;
				opacity: 0;
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}

			p {
				cursor: pointer;
				color: #b0c4e0;
			}

			.icon-font {
				font-size: 16px;
			}
		}
	}
	// broadcast :: end

	// invoicing
	&__invoicing {
		&-info {
			p {
				font-size: 16px;
				letter-spacing: 0.38px;
			}
		}

		table {
			.nowrap {
				white-space: nowrap;
			}

			thead,
			tbody {
				th,
				td {
					@include adaptive(767) {
						min-width: 150px;
					}

					&:first-of-type {
						padding-left: 0;
					}
				}
			}

			thead {
				th {
					border-top: none;
					border-bottom-width: 1px;
					font-size: 14px;
					font-weight: bold;
				}
			}

			tbody {
				tr {
					&:last-of-type {
						td {
							border-bottom: 1px solid #dee2e6;
						}
					}

				}

				td {
					font-size: 16px;
				}
			}
		}
	}
	// invoicing :: end
}

// SECTION subscribers :: END
